<template>
		<scroll :data='courses' ref='warpper'>
			<list :data="courses">
			</list>
		</scroll>
</template>
<script>
import List from 'components/list/list'
import scroll from 'components/base/scroll'
	export default{
		props:{
			tit:{
				type:String,
				default:''
			}
		},
		data(){
			return {
				title:this.$route.query.tit,
				courses:[],
				type:''
			}
		},
		created(){
			this.getData()
		},
		methods:{
			getData(){
				this.$http.get('http://learningApp/api').then((res)=>{
						console.log(res.body.course)
						this.courses=res.body.course	
				})
			}
		},
		components:{
			List,
			scroll
		},
		watch:{
			'$route'(){
				let type=this.$route.params.type
				let clone=this.courses.slice()
				if(type==='jc'){
					this.$http.get('http://learningApp/api').then((res)=>{
						this.courses=res.body.course.slice(0,10)
					})
								
				}
				if(type==='cj'){
					this.$http.get('http://learningApp/api').then((res)=>{
						this.courses=res.body.course.slice(10,15)
					})
				}
				if(type==='zj'){
					this.$http.get('http://learningApp/api').then((res)=>{
						this.courses=res.body.course.slice(3,7)
					})

				}
				if(type==='gj'){
					this.$http.get('http://learningApp/api').then((res)=>{
						this.courses=res.body.course.slice(2,20)
					})
				}
				if(type==='all'){
					this.getData()
				}
			}
		},

	}
</script>
<style lang="stylus" scoped>
	.wrapper
		position fixed
		width 100%
		top 0.9rem
		background #fff
		padding 0.05rem 0
		border-radius 10px
		bottom 0
		.list-tit
			font-size 0.16rem
			text-align left
			padding 0 0.1rem
			height 0.35rem
			line-height 0.35rem 
			span
				font-weight bold
</style>


