<template>
	<transition enter-active-class='animated bounceIn' leave-active-class='animated bounceOut'>
	<div class="more">
		<div class="content">
			<h3>{{headtitle}}</h3>
			<mt-radio class='box'
			  align="right"
			  v-model='value'
			  :options="data"
			  >
			</mt-radio>
			<i class="icon-close2" @click='close'></i>
		</div>
	</div>
	</transition>
</template>
<script>
import { Radio } from 'mint-ui'
	export default{
		props:{
			data:{
				type:Array,
				default:[]
			},
			headtitle:{
				type:String,
				default:''
			}
		},
		data(){
			return {
				value:''
				
			}
		},
		methods:{
			close(){
				this.$emit('closeMore',this.value)
			}
		},
		components:{
			Radio
		}
	}
</script>
<style lang="stylus" scoped>
	.more
		position fixed
		width 100%
		height 100%
		left 0
		right 0
		bottom 0
		top 0
		background rgba(0,0,0,0.8)
		z-index 200
	.content
		width 90%
		margin 0 auto
		background #fff
		border-radius 10px
		margin-top 50%
		transform translate3d(0,-25%,0)
		padding 0.1rem 0
		h3
			font-size 0.16rem
			height 0.45rem
			line-height 0.45rem
			text-align left
			padding-left 0.1rem
			margin 0 0.2rem
			border-bottom 1px solid #efefef
		.box
			width 100%
		.icon-close2
			position absolute
			font-size 0.3rem
			color #fff
			bottom -0.8rem
			padding 0.1rem
			left 50%
			transform translate3d(-25%,0,0)
			border 1px solid #fff
			border-radius 100%
		
	
		

</style>


