<template>
	<div class="commentBox">
		<div class="title">
			<span class="tit">评论({{num}}条)</span>
			<span class="icon-back" @click='back'></span>
		</div>
		<commentList :data='commentList' class='content'></commentList>
		<div class="inp">
			<input class="text" type="text" v-model='text' placeholder="我要评论">
			<span @click='sendMsg'>发送</span>
		</div>
	</div>
</template>
<script>
import commentList from 'components/commentList/commentList'
	export default{
		data(){
			return {
				commentList:[],
				text:'',
				num:0
			}
		},
		components:{
			commentList
		},
		created(){
			this.getData()
		},
		methods:{
			sendMsg(){
				let sendData={
					avata:"http://img.zcool.cn/community/04342f58671d7aa8012060c8a551f6.jpg@80w_80h_1c_1e_1o_100sh.jpg",
					user:"小新",
					text:this.text,
					time:new Date().toLocaleString()
				}
				this.commentList.unshift(sendData)
				console.log(this.commentList)
			},
			getData(){
				this.$http.get('http://learningApp/api').then((res)=>{
						this.commentList=res.body.comment
						this.num=res.body.comment.length
				})
			},
			back(){
				this.$router.go(-1)
			}
		}
	}
</script>
<style lang="stylus" scoped>
.commentBox
	position fixed
	left 0
	top 0
	width 100%
	background #fff
	height auto
	z-index 200
	height 100%
	.title
		position fixed
		width 100%
		height 0.45rem
		line-height 0.45rem
		background #fff
		font-size 0.18rem
		text-align center
		z-index 100
		.tit
			font-size 0.20rem
			font-weight bold
		.icon-back
			position absolute
			left 0.1rem
			top 0.12rem
			font-size 0.20rem
	.content
		position fixed
		top 0.45rem
		bottom 0
		width 100%
		background #f5f5f5
		padding-top 0.20rem
		border-radius 10px
	.inp
		position fixed 
		left 0
		bottom 0
		height 0.5rem
		line-height 0.5rem
		background #fff
		padding 0 2%
		font-size 0.16rem
		width 96%
		.text
			width 80%
			float left
			height 0.35rem
			line-height 0.35rem
			margin-top 0.07rem
			border 1px solid #ccc
			border-radius 50px
			background #f5f5f5
			padding-left 0.2rem
		span
			display inline-block
			width 10%
			float right 
			font-size 0.16rem
		
</style>


