<template>
	<div class="social">
		<span  :class="{'icon-shoucang':active,'icon-shoucang1':!active}"  @click='heart'></span>
		<span class="icon-comment" @click='comment'></span>
		<span class="icon-zhuanfa2"></span>
	</div>
</template>
<script>
	export default{
		props:{
				active:{
					type:Boolean,
					defult:false
				}	
			},
		data(){
			return {
				
				
			}
		},
		methods:{
			heart(){
				this.$emit('shouc')
			},
			comment(){
				this.$emit('moreData')
			}
		}
	}
</script>
<style lang="stylus" scoped>
	.social
		display flex
		font-size 0.25rem
		position fixed
		width 100%
		background #fff
		bottom 0
		left 0
		z-index 100
		span
			height 0.6rem
			line-height 0.6rem
			display inline-block
			flex 1

</style>		



