<template>
	<div class="videoDetail">
		<div class="navBar" ref='topNav'>
			<span>视频详情</span>
			<i class="icon-back" @click='back'></i>
		</div>
		<div class="box" ref='imgbox'>
			<audio :src="url" ></audio>
			<img :src="url" alt="">
		</div>
		<scroll class="content" :probeType="probeType" :listenScroll="listenScroll" :data='commentList' @scroll='domScroll' ref='content'>
			<div style='background:#f5f5f5; border-radius:10px 10px 0 0'>
				<div class="tit">
					<h1>{{title}}</h1>
					<p>
						<router-link :to="{name:'courseIntro',query:{t:title,a:avata,d:des}}" tag='div'>	
							<span><img :src="avata" alt=""></span>
							<span>{{des}}</span>
							<span class="icon-more2"></span>
						</router-link>
					</p>
				</div>
				<div class="courseItem">
					<h3>课程描述</h3>
					<ul>
						<li class="item" v-for='(item,index) in courseIntro'>{{index+1}}.{{item.title}}
							<span v-show='index===1 || index === 3 || index===5' class="icon-rank"></span>
							<span v-show='index===1 || index === 3 || index===5' class="icon-xiazai"></span>
							<span v-show='index===2 || index === 4 || index===6' class="icon-checkedon"></span>
						</li>
					</ul>
				</div>

				<div class="conmmentCon">
					<h3>评论({{num}}) <i class="icon-more" @click='more'></i></h3>
					<commentList :data='commentList'></commentList>
				</div>
				
			</div>
		</scroll>
		<social @shouc='heart' :active='heartac' @moreData='more'></social>
		<router-view></router-view>	
	</div>
</template>
<script>
import commentList from 'components/commentList/commentList'
import scroll from 'components/base/scroll'
import social from 'components/social/social'
	export default{
		data(){
			return{
				scrollY:0,
				probeType:3,
				heartac:false,
				listenScroll:true,
				commentList:[],
				num:0,
				url:this.$route.query.u,
				title:this.$route.query.t,
				avata:this.$route.query.a,
				des:this.$route.query.d,
				courseIntro:[
					{
						title:'产品摄影的介绍和特色',
						videoUrl:''
					},
					{
						title:'产品拍照前的准备',
						videoUrl:''
					},
					{
						title:'前期拍摄参数的设置',
						videoUrl:''
					},
					{
						title:'掌握相机三大控件',
						videoUrl:''
					},
					{
						title:'详解对焦和白平衡',
						videoUrl:''
					},
					{
						title:'初学者对曝光、测光的掌握',
						videoUrl:''
					},
					{
						title:'构图，让摄影更美',
						videoUrl:''
					}
				]
			}
		},
		components:{
			commentList,
			social,
			scroll
		},
		created(){
			this.getData()
		},
		methods:{
			domScroll(pos){
				this.scrollY=pos.y
			},
			heart(){
				this.heartac=!this.heartac
			},
			more(){
				this.$router.push({path:'/comment'})
			},
			back(){
				this.$router.go(-1)
			},
			getData(){
				this.$http.get('http://learningApp/api').then((res)=>{
						this.commentList=res.body.comment.slice(0,3)
						this.num=res.body.comment.length
				})
			},
		},
		watch:{
			scrollY(newY){

				let transY=Math.max(-this.imgHeihgt+45,newY)/100
				console.log(newY+'--------------'+transY)
				this.$refs.content.$el.style.transform ='translate3d(0,'+transY+'rem,0)'
				let percent=Math.abs(newY/this.imgHeihgt)
				let scale=1+percent
				let blur=Math.min(10*percent,10)
				if(-newY>=100){
					this.$refs.topNav.style.background='#fff'
					this.$refs.topNav.style.color='#000'
				}else{
					this.$refs.topNav.style.background='none'
					this.$refs.topNav.style.color='#fff'
				}
				if(newY>=0){
					this.$refs.imgbox.style.transform='scale('+scale+')'
					this.$refs.imgbox.style.webkitTransform='scale('+scale+')'
				}else{
					this.$refs.imgbox.style.filter='blur('+blur+'px)'
					this.$refs.imgbox.style.webkitFilter='blur('+blur+'px)'
				}
			}
		},
		mounted(){
			this.imgHeihgt=this.$refs.imgbox.clientHeight
			console.log(this.imgHeihgt)
			this.$refs.content.$el.style.top=this.$refs.imgbox.clientHeight/100+'rem'
		}
	}
</script>
<style lang="stylus" scoped>
.videoDetail
	.navBar
		font-size 0.18rem
		text-align center
		height 0.45rem
		line-height 0.45rem
		position fixed
		z-index 100
		width 100%
		color #fff
		.icon-back
			font-size 0.2rem
			position absolute 
			left 0.1rem
			top 0.1rem

	.box
		width 100%
		height 2.5rem
		background #000
		position fixed
		img
			width 100%
	.content
		top 2.4rem
		position fixed
		z-index 10
		border-radius 10px
		padding-bottom 0.2rem
		background #F5F5F5
		bottom 0
		.tit	
			height 0.8rem
			font-size 0.16rem
			padding 0.1rem
			h1
				height 0.4rem
				line-height 0.4rem
				font-size 0.18rem
				text-align left
				font-weight bold
			p
				width 100%
				height 0.3rem
				line-height 0.3rem
				text-align left
				color #6e6e6e
				position relative

				img
					height 0.3rem
					border-radius 100%
					vertical-align middle
					margin-right 0.05rem
				.icon-more2
					display inline-block
					text-align right
					position absolute 
					right 0
					top 0.1rem
		.courseItem
			margin 0 2%
			font-size 0.16rem
			background #fff
			border-radius 8px
			h3
				height 0.3rem 
				line-height 0.3rem
				text-align left
				padding 0 0.1rem
			.item
				height 0.4rem
				line-height 0.4rem
				text-align left
				padding 0  0.1rem
				font-weight bold
				.icon-rank
					color #40DC9B
				.icon-xiazai
					float right
					padding-top 0.1rem
					font-size 0.18rem
				.icon-checkedon
					float right
					padding-top 0.1rem
					color #40DC9B
					font-size 0.2rem
	.conmmentCon
		margin 0 1%
		background #fff
		margin-top 0.2rem
		border-radius 10px
		h3
			height 0.4rem
			line-height 0.4rem
			font-size 0.18rem
			text-align left
			padding 0 0.2rem
			.icon-more
				font-size 0.2rem
				float right
				padding-top 0.1rem
</style>


