<template>
	<div class="columnlist">
		<ul>
			<li class="listItem" v-for='(item,index) in data' :key='index' @click='select(item)' :class="{'active':columnNum}">
				<div class="imgBox" :class="{'active':columnNum}">
					<img class="timg" v-lazy="item.url" alt="">
				</div>
				<div class="txtbox">
					<h3 class="title">{{item.title}}</h3>
					<p><span v-for='(tag,index) in item.tags' :key='index'>{{tag}}</span></p>
					<p><span><img :src="item.avata" alt=""></span>  <span>{{item.des}}</span><span v-show='!columnNum'>  | </span><span class="time" v-show='!columnNum'>发布于：{{item.time}}</span></p>	
				</div>
			</li>
		</ul>
	</div>
</template>
<script>
	export default{
		data(){
			return {

			}
		},
		props:{
			data:{
				type:Array,
				default:[]
			},
			columnNum:{
				type:Boolean,
				default:false
			}
		},
		computed:{
			
		},
		methods:{
			select(item){
				this.$emit('selectItem',item)
			}
		}
		
	}
</script>
<style lang="stylus" scoped>
.columnlist
	background #f5f5f5
	padding 0.2rem 0
	.listItem
		width 100%
		float left
		font-size 0.16rem
		transition all ease 0.3s
		&.active
			width 50%
		.imgBox
			width 98%
			height 1.7rem
			overflow hidden
			border-radius 8px
			margin 0 1%
			&.active
				height 1.2rem
			.timg
				width 100%
		.txtbox
			margin 0 1%
			margin-bottom 0.1rem
			padding 0 0.05rem
			padding-bottom 0.1rem
			background #ffff
		h3
			font-size 0.16rem
			height 0.4rem
			line-height 0.4rem
			text-align left
			font-weight bold
			overflow hidden
			text-overflow:ellipsis
			white-space: nowrap
		p
			text-align left
			height 0.3rem
			line-height 0.3rem
			font-size 0.13rem
			color #ccc
			overflow hidden
			text-overflow:ellipsis
			white-space: nowrap
			span 
				padding 0 0.05rem
			img
				width 0.25rem
				border-radius 100%
				vertical-align middle





</style>


