<template>
	<scroll class='commentList' :data='data'>
		<div>
			<div class="listitem" v-for='item in data'>
				<h3>
					<span><img :src="item.avata" alt=""></span>
					<span>{{item.user}}</span>
					<span class="time">{{item.time}}</span>
				</h3>
				<div class="content">
					{{item.text}}
				</div>
			</div>
		</div>
	</scroll>
</template>
<script>
import scroll from 'components/base/scroll'
	export default{
		props:{
			data:{
				type:Array,
				default:[]
			}
		},
		components:{
			scroll
		}

	}

</script>
<style lang="stylus" scoped>
	.listitem
		margin 0.1rem 0
		padding 0.1rem 0.2rem
		border-bottom 1px solid #F0F0F0
		background #fff
		border-radius 8px
		h3
			height 0.3rem
			line-height 0.3rem
			text-align left 
			padding 0
			font-size 0.16rem
			color #6f6f6f
			span
				font-weight bold
			img
				width 0.3rem
				border-radius 100%
				vertical-align middle
			.time
				float right 
				margin-top 0
				font-size 0.13rem
		.content
			font-size 0.14rem
			padding-left 0.3rem
			padding-right 0
			text-align left
			line-height 0.30rem
			color #a8a8a8
			font-weight bold
</style>


