<template>
	<div class="wrapper" ref='wrapper'>
		<slot></slot>
	</div>
</template>
<script>
import BScroll from 'better-scroll'
import {Indicator} from 'mint-ui';
	export default{
		props:{
			probeType:{
				type:Number,
				default:1
			},
			click:{
				type:Boolean,
				default:true
			},
			data:{
				type:Array,
				default:[]
			},
			listenScroll:{
				type:Boolean,
				default:false
			}
		},
		components:{
			Indicator
		},
		methods:{
			_initScroll(){
				if(!this.$refs.wrapper){
					return
				}
				if(!this.scroll){
					this.scroll=new BScroll(this.$refs.wrapper,{
						probeType:this.probeType,
						click:this.click
					})
				}else{
					this.refresh()
				}

				if(this.listenScroll){
					let that = this
					this.scroll.on('scroll',(pos)=>{
						that.$emit('scroll',pos)
					})
				}
			},
			refresh(){
				this.scroll && this.scroll.refresh()
			}
		},
		watch:{
			data(){
				var that=this
				Indicator.open({
				  text: '加载中...',
				  spinnerType: 'fading-circle'
				});
				setTimeout(()=>{
					that.refresh()
					Indicator.close()
				},200)
			}
		},
		mounted(){
			var that=this
			Indicator.open({
			  text: '加载中...',
			  spinnerType: 'fading-circle'
			});
			setTimeout(()=>{
				that._initScroll()
				Indicator.close()
			},200)
		}
	}
</script>
<style lang="stylus" scoped>



</style>


