<template>
	<div class="home">
		<myhead :searchShow='searchInput' @searchbtn='search' @search='changeShow' @inpCli='focus' ref='header'></myhead>
		<scroll class="content" ref='wrapper' :data='courses'>
			<div class='pd06'>
				<mt-swipe :auto="0" class="slide">
				  <mt-swipe-item v-for='(item,index) in slides' :key='index'><img v-lazy="item.url" alt=""></mt-swipe-item>
				</mt-swipe>
				<div class="icons">
					<div class="icons-list">
						<span class="list-item"><img src="./icon1.png" alt=""><p>推荐大咖</p></span>
						<span class="list-item"><img src="./icon2.png" alt=""><p>订阅频道</p></span>
						<span class="list-item"><img src="./icon3.png" alt=""><p>优惠券</p></span>
						<span class="list-item"><img src="./icon4.png" alt=""><p>我的课程</p></span>
						<span class="list-item"><img src="./icon5.png" alt=""><p>下载中心</p></span>
					</div>
					<div class="icons-list">
						<span class="list-item"><img src="./icon6.png" alt=""><p>充值中心</p></span>
						<span class="list-item"><img src="./icon7.png" alt=""><p>扫码看课</p></span>
						<span class="list-item"><img src="./icon9.png" alt=""><p>我的收藏</p></span>
						<span class="list-item"><img src="./icon10.png" alt=""><p>站酷推荐</p></span>
						<span class="list-item"><img src="./icon8.png" alt=""><p>设置中心</p></span>
					</div>
				</div>

				<div class="intro">
					<div class="intro-item">
						<h3 class="title">
							<span class="text">我的课程</span>
							<span class="icon-more"></span>
						</h3>
						<ul class="list">
							<li v-for='item in intro1' class="item">
								<span>{{item.title}}</span>
								<span class="des">{{item.des}}</span>
								<mt-progress :value="item.value" class='progress'>
								  <div slot="start"></div>
								  <div slot="end" class="end">{{item.value}}%</div>
								</mt-progress>
							</li>
						</ul>
					</div>
					<div class="intro-item" style='height:4.805rem'> 
						<h3 class="title">
							<span class="text">最新课程</span>
							<span class="icon-more"></span>
						</h3>
						<!-- -->
						<list :data='courses'></list>
					</div>
					<div class="intro-item" style='height:4.805rem'> 
						<h3 class="title">
							<span class="text">热门推荐</span>
							<span class="icon-more"></span>
						</h3>
						<list :data='recomends'></list>
					</div>
					<div class="intro-item" style='height:4.805rem'> 
						<h3 class="title">
							<span class="text">专栏推荐</span>
							<span class="icon-more"></span>
						</h3>
						<column :data='columnList' class='columnbox' ></column>
					</div>
				</div>
			</div>
		</scroll>
		<transition enter-active-class='animated fadeInDown' leave-active-class='animated fadeOut'>
			<div class="history" v-show='historyShow' >
				<div class="mark" @click='hideHis'></div>
				<div class="listbox">
					<ul>
						<li v-for='item in hisList' class="listitem" @click='getHis(item.title)'>
							<span>{{item.title}}</span>
							<span class="icon-close5" @click='delet'></span>
						</li>
					</ul>
				</div>
			</div>
		</transition>
	
	</div>
</template>
<script>
import mockData from '@/mock.js'
import scroll from 'components/base/scroll'
import { Swipe, SwipeItem, Progress, MessageBox, Toast, Indicator} from 'mint-ui';
import List from 'components/list/list'
import myhead from 'components/header/header'
import column from 'components/columnlist/columnlist'
	export default{
		data(){
			return {
				historyShow:false,
				searchInput:false,
				hisList:[{
					title:'摄影教程'
				},
				{
					title:'后期处理'
				},
				{
					title:'人像摄影'
				},
				{
					title:'特效处理'
				},
				{
					title:'ps技术'
				},
				{
					title:'AI技术'
				}],
				slides:[
					{
						url:'http://image.gogoup.com/bannerNew/1515479131225.jpg'
					},
					{
						url:'http://image.gogoup.com/bannerNew/1515131991006.jpg'
					},{
						url:'http://image.gogoup.com/bannerNew/1515567872123.jpg'
					},{
						url:"http://image.gogoup.com/bannerNew/1514198990660.jpg"
					}
				],
				intro1:[
					{
						title:'产品摄影后期精修',
						des:"周大鹏老师",
						value:60
					},
					{
						title:'产品摄影后期精修',
						des:"周大鹏老师",
						value:30
					}
				],
				courses:[],
				recomends:[],
				columnList:[]
			}
		},
		components:{
			Swipe,
			SwipeItem,
			Progress,
			List,
			myhead,
			column,
			scroll,
			MessageBox,
			Toast,
			Indicator
		},
		created(){
			this.getData()
		},
		methods:{
			getHis(value){
				this.$refs.header.hisTxt(value)
			},
			getData(){
				this.$http.get('http://learningApp/api').then((res)=>{
						/*Indicator.open({
						  text: '加载中...',
						  spinnerType: 'fading-circle'
						});*/
						setTimeout(()=>{
							this.courses=res.body.course.slice(0,4)
							this.recomends=res.body.course.slice(4,8)
							this.columnList=res.body.recomends.slice(0,2)
							/*Indicator.close()*/
						},1000)
						
					
					
				})

				/*new Promise((resolve, reject) => {
					console.log(mockdata.course)
				    //that.courses =mockdata.data.course;  //
				     }).catch(err=>{
				      console.log(err)
				})*/
				/*this.$http.get('/api/course').then((res)=>{
						if(res.body.error===0){
							let data=res.body.data
							this.courses=data.slice(0,4)
							this.recomends=data.slice(4,8)
						}
					
					
				})
				this.$http.get('/api/column').then((res)=>{
						if(res.body.error===0){
							let data=res.body.data
							this.columnList=data.slice(0,2)
							
						}
				})*/
			},
			changeShow(){
				this.searchInput=!this.searchInput
			},
			search(value){
				this.historyShow=false
				console.log(value)

			},
			focus(){
				console.log('ss')
				this.historyShow=!this.historyShow
			},
			hideHis(){
				this.historyShow=false
			},
			delet(){
				MessageBox.confirm('确定要删除本条记录?').then(action => {
  						Toast('删除成功');
				});
				/*MessageBox({
				  title: '提示',
				  message: '确定要删除本条记录?',
				  showCancelButton: true
				})*/
			},
			_initScroll(){
				if(!this.$refs.wrapper){
					return
				}
				if(!this.scroll){
					this.scroll=new BScroll(this.$refs.wrapper,{
						click:this.true
					})
				}else{
					this.refresh()
				}
			}
		},
		mounted(){
			

		}
	}
</script>
<style lang="stylus" scoped>
	.pd06
		padding-bottom 0.6rem
	.history
		position fixed
		width 100%
		top 0.45rem
		height 100%
		z-index 100
		.mark
			position absolute
			width 100%
			height 100%
			background rgba(0,0,0,0.5)
			z-index -1
		.listbox
			background #F6F5F8
			border-radius 0 0 10px 10px
			.listitem
				text-align left
				margin 0 0.2rem
				font-size 0.14rem
				height 0.45rem
				line-height 0.45rem
				border-bottom 1px solid #dedede
				color #ccc
				.icon-close5
					float right 
					margin-top 0.15rem
	.content
		position fixed
		width 100%
		top 0.45rem
		left 0
		right 0
		bottom 0
		.slide
			height 1.25rem
			overflow hidden
			width 100%
			img
				width 100%
		.icons
			padding 0.1rem
			background #fff
		.icons-list
			display flex
			.list-item
				height .6rem
				flex 1
				display block
				padding 0.1rem 0
				img
					width 0.4rem
					display block
					margin 0 auto
				p
					font-size 0.14rem
					padding 0.1rem 0
					font-weight bold
		.intro
			background #F5F5F5
			.intro-item
				clear both
				h3
					height 0.45rem
					line-height 0.45rem
					background #F5F5F5
					font-size 0.14rem
					text-align right
					position relative
					.text
						font-weight bold
						display block
						float left
						padding-left 0.1rem
					.icon-more
						display inline-block
						padding-right 0.1rem
						font-size 0.18rem
						font-weight bold
				.list
					height 1.2rem
					display flex
					font-size 0.18rem
					.item
						flex 1
						width 96%
						border-radius 5px
						margin 1%
						background -webkit-gradient(linear, left top, right bottom, from(#35C3FC), to(#57e397))
						padding 0.15rem 0
						span 
							text-align left
							padding-left 0.1rem
							height 0.3rem
							line-height 0.3rem
							display block
							color #fff
						.des
							font-size 0.14rem
							opacity 0.5
						.progress
							width 90%
							padding 0 5%
							color #fff
							.end
								position absolute
								right 0.1rem
								bottom 0.1rem
								font-size 0.14rem
			.columnbox
				padding-top 0
	




					
</style>


