<template>
	
	<div class='listbox'>
		<slot></slot>
		<div ref='item' class="course-item" :key='index' v-for='(item,index) in data' @click='selectItem(item)'>
			<div class="imgbox"><img v-lazy="item.url" alt="">
				<span class="price">￥{{item.price}}</span></div>
			<p class="course-title">{{item.title}}</p>
			<p class="course-des"><span><img class="avata" :src="item.avata" alt=""></span><span>{{item.des}}</span></p>
		</div>
	</div>
	
</template>
<script>
	export default{
		props:{
			data:{
				type:Array,
				default:[]
			}
		},
		components:{
			
		},
		methods:{
			selectItem(item){
				this.$router.push({path:'/videoDetail',query:{u:item.url,t:item.title,d:item.des,a:item.avata}})
			}
		},
		mounted(){
			
		}
	}
</script>
<style lang="stylus" scoped>
		.listbox
			position absolute
			width 100%
			background #FFF
			border-radius 10px
			.course-item
				width 48%
				margin 1%
				float left
				font-size 0.16rem
				text-align left
				padding-bottom 3%
				background #fff
				border-radius 5px 
				.imgbox
					width 100%
					position relative
					.price
						position absolute
						font-size 0.16rem
						padding 0.03rem
						background #333
						color #fff
						right 0.01rem
						bottom 0.03rem
					img
						width 100%
						border-radius 5px
				.course-title
					height 0.4rem
					line-height 0.4rem
					font-size 0.14rem
					font-weight bold
					margin-left 0.05rem
				.course-des
					height 0.2rem
					line-height o.2rem
					verticle-align center
					margin-left 0.05rem
					img
						width 0.2rem
						border-radius 100%
						vertical-align middle
						margin-right 0.05rem
					span
						display inline-block
						font-size 0.12rem
</style>


