<template>
	<div class="title" style="z-index:100">
		<slot></slot>
			
		<div class="logo" v-show='!searchShow'>
			<img v-show='!title' src="./logo.png" alt="">
			<span v-show='title' @click='listDown'>{{title}} <i class="icon-moreunfold"></i></span>
		</div>
		
		<transition enter-active-class='animated fadeIn' leave-active-class='animated fadeOut'>
		<div class="search" v-show='searchShow'>
			<input type='text' placeholder="请输入要搜索的内容" v-model='searchText' @click='inpClick'>
		</div>
		</transition>
		<span class='icon-search' @click='search' v-show='!searchShow'></span>
		<span class='icon-search' v-show='searchShow' @click='searchbtn'></span>
	</div>
</template>
<script>
	export default{
		props:{
			title:{
				type:String,
				default:''
			},
			searchShow:{
				type:Boolean,
				default:false
			}
		},
		data(){
			return {
				searchText:'',

			}
		},
		methods:{
			hisTxt(value){
				this.searchText=value
			},
			listDown(){
				this.$emit('dropDown')
			},
			search(){
				this.$emit('search')

			},
			searchbtn(){
				this.$emit('searchbtn',this.searchText)
			},
			inpClick(){
				this.$emit('inpCli')
			}
		}
	}
</script>
<style lang="stylus" scoped>
	.search
		height 0.45em
		text-align left
		padding-left 0.2rem
		input
			position relative
			font-size 0.14rem
			height 0.3rem
			width 80%
			top -0.28rem
			border 1px solid #f5f5f5
			background #f5f5f5
			border-radius 20px
			padding-left 0.1rem
	.title
		height 0.45rem 
		line-height 0.45rem
		background #fff
		position fixed
		text-align center
		width 100%
		.logo
			height 100%
			font-size 0.2rem
			span,i
				font-weight bold
			img
				display block
				margin 0 auto
				height 0.2rem
				padding-top 0.12rem
		.icon-search
			position absolute
			top 0.12rem 
			right 0.10rem 
			font-size 0.2rem
			font-weight bold
</style>


