<template>
	<div class="detailBox">
		<div class="title">
			<span class="tit">详情</span>
			<span class="icon-back" @click='back'></span>
		</div>
		<div class="contentBox">
			<div class="box">
				<h3 class="tit">{{title}}</h3>
				<p class="p1">
					<span v-for='(tag,index) in tags' :key='index'>{{tag}}</span>
				</p>
				<p class="p1">
					<span><img :src="avata" alt=""></span>  
					<span>{{des}}</span>
					<span>  | </span>
					<span class="time">发布于：{{time}}</span>
				</p>
				<i class="look">订阅</i>
			</div>
			<div class="content">
				<img :src="url" alt="">
				<div>
					<p>平铺直叙的故事是乏味的。一部好的小说、电影，大多充满了起伏和转折。类似的，一幅好的摄影作品里，通常也包含着各种各样的对比和冲突。没有形式上的对比，照片缺少吸引观者的兴趣点，显得画面乏味。没有内涵上的冲突，照片难以引发读者的思考，让人过目即忘。</p>

　　				<p>通过寻找对比这简单一招，我们就能极大的提升照片吸引力。</p>
					<p>1、形式和内涵两种对比</p>

				　　<p>我们提到对比和冲突的时候，其实指的是两种情况，一种是形式上、也就是画面上的对比，比如大小、明暗、冷暖等等。另外一种是内涵上的对比，比如战争与和平，人类与自然，悲伤和喜悦等等。形式上的对比，让画面充满了表现力，吸引读者的目光。
				</p>
　　				<p>比如下面这张风光摄影师Max Rive的照片，画面左侧的冷色和暗调，与画面右方的暖色和亮调，形成了强烈的对比。画面中明暗冷暖的冲突，不仅展现了一幅波澜壮阔的全景画面，还牢牢的抓住的读者的眼球，让人惊叹不已。</p>
				</div>
			</div>
			<div class="conmmentCon">
				<h3>评论({{num}}) <i class="icon-more" @click='more'></i></h3>
				<commentList :data='commentList'></commentList>
			</div>
		</div>
		<social @shouc='heart' :active='heartac' @moreData='more'></social>
				
	</div>
</template>
<script>
import social from 'components/social/social'
import commentList from 'components/commentList/commentList'
	export default{
		data(){
			return {
				url:this.$route.query.url,
				title:this.$route.query.title,
				des:this.$route.query.des,
				tags:this.$route.query.tags,
				time:this.$route.query.time,
				avata:this.$route.query.avata,
				heartac:false,
				commentList:[],
				num:0
			}
		},
		components:{
			social,
			commentList
		},
		created(){
			this.getData()
		},
		methods:{
			getData(){
				this.$http.get('/api/comment').then(function(res){
					if(res.body.error===0){
						let data=res.body.data
						this.commentList=data.slice(0,3)
						this.num=data.length
					}
				})
			},
			back(){
				this.$router.go(-1)
			},
			heart(){
				this.heartac=!this.heartac
			},
			more(){
				this.$router.push({path:'/comment'})
			}
		}
	}
</script>
<style lang="stylus" scoped>
	.title
		position fixed
		width 100%
		height 0.45rem
		line-height 0.45rem
		background #fff
		font-size 0.18rem
		text-align center
		z-index 100
		.tit
			font-size 0.20rem
			font-weight bold
		.icon-back
			position absolute
			left 0.1rem
			top 0.12rem
			font-size 0.20rem
	.contentBox
		font-size 0.16rem
		line-height 0.24rem
		position absolute 
		top 0.45rem
		left 0
		width 100%
		background #f5f5f5
		border-radius 10px
		z-index 50
		.box
			position relative
			.look
				position absolute 
				right 0.1rem
				bottom 0
				padding 0 0.1rem
				border-radius 20px
				border 1px solid #57e397
				font-size 0.12rem
				color #57e397
		h3
			font-size 0.16rem
			height 0.4rem
			line-height 0.4rem
			text-align left
			font-weight bold
			padding 0 0.2rem
		.p1
			text-align left
			height 0.3rem
			line-height 0.3rem
			font-size 0.13rem
			color #ccc
			overflow hidden
			text-overflow:ellipsis
			white-space: nowrap
			padding 0 0.2rem
			span 
				padding 0 0.05rem
			img
				width 0.25rem
				border-radius 100%
				vertical-align middle

		.ctit
			height 0.4rem
			line-height 0.4rem
			font-size 0.18rem
			font-weight bold
		.des
			height 0.4rem
			line-height 0.4rem
			font-size 0.14rem


	.content
		padding 0 0.1rem
		p
			text-indent 2em
			text-align left
		img
			width 98%
			margin-top 0.2rem
	.conmmentCon
		margin 0 1%
		background #fff
		margin-top 0.2rem
		border-radius 10px
		h3
			height 0.4rem
			line-height 0.4rem
			font-size 0.18rem
			.icon-more
				font-size 0.2rem
				float right
				padding-top 0.1rem


		
</style>


