<template>
	<div class="course">
		<myhead :title='title' @dropDown='moreClass'></myhead>
		<div class="tab">
			<div class="tab-item">
				<router-link :to="{name:'listDetail',params:{type:'all'}}">
					<span class="active">全部</span>
				</router-link>
			</div>
			<div class="tab-item">
				<router-link :to="{name:'listDetail',params:{type:'jc'}}">
					<span>基础入门</span>
				</router-link>
			</div>
			<div class="tab-item">
				<router-link :to="{name:'listDetail',params:{type:'cj'}}">
					<span>初级拔高</span>
				</router-link>
			</div>
			<div class="tab-item">
				<router-link :to="{name:'listDetail',params:{type:'zj'}}">
					<span>中级进阶</span>
				</router-link>
			</div>
			<div class="tab-item">
				<router-link :to="{name:'listDetail',params:{type:'gj'}}">
					<span>高级突破</span>
				</router-link>
			</div>
			<div class="radius"></div>
		</div>
		
			<router-view :tit='classTitle'></router-view>
		
		
		<more v-show='moreShow' :data='courseClass' :headtitle='classTitle'  @closeMore='hideMore'></more>
	</div>
</template>
<script>
import Foot from 'components/foot/foot'

import myhead from 'components/header/header'
import more from 'components/moreClass/moreClass'
	export default{
		data(){
			return {
				moreShow:false,
				classTitle:'课程分类',
				title:'摄影艺术',
				courseClass:[
					{
						label:'全部',
						value:'全部'
					},{
						label:'摄影艺术',
						value:'摄影艺术'
					},{
						label:'摄影后期',
						value:'摄影后期'
					},{
						label:'绘画插画',
						value:'绘画插画'
					},{
						label:'平面设计',
						value:'平面设计'
					},{
						label:'影视动画',
						value:'影视动画'
					},{
						label:'公开课',
						value:'公开课'
					},{
						label:'UI设计',
						value:'UI设计'
					},{
						label:'三维',
						value:'三维'
					}
				],
			}
		},
		components:{
			Foot,
			myhead,
			more
		},
		created(){
			this.$router.push({path:'/course/listDetail/all'})
		},
		methods:{
			moreClass(){
				console.log('ss')
				this.moreShow=!this.moreShow
				
			},
			hideMore(value){
				console.log(value)
				this.title=value
				this.moreShow=false
			}
		}
	}
</script>
<style lang="stylus" scoped>
	.tab
		position fixed
		width 100%
		top 0.45rem
		font-size 0.16rem
		display flex
		height 0.65rem
		line-height 0.55rem
		background -webkit-gradient(linear, right bottom, left top, from(#35C3FC), to(#57e397))
		border-radius 10px 10px 0 0
		z-index 100
		.radius
			position absolute 
			width 100%  
			height 0.15rem
			background #fff
			top 0.55rem
			border-radius 10px 10px 0 0

		.tab-item
			flex 1
			text-align center
			color #fff
			span
				font-weight bold
				padding-bottom 0.04rem
				color #fff
				opacity 0.8
				font-size 0.14rem
			.router-link-active
				span 
					border-bottom 1px solid #fff
					opacity 1
	.title
		height 0.45rem 
		line-height 0.45rem
		background #fff
		position fixed
		text-align center
		width 100%
		.logo
			height 100%
			img
				display block
				margin 0 auto
				height 0.2rem
				padding-top 0.12rem
		.icon-search
			position absolute
			top 0.12rem 
			right 0.10rem 
			font-size 0.2rem
			font-weight bold
	.footbox
		position fixed 
		width 100%
		bottom 0
		left 0 
		right 0
</style>


