<template>
	<div class="message">
		<h3 ref='title'>消息中心</h3>
		<div class="list" ref='list'>
			<div class="listItem"><router-link :to="{name:'messageList',query:{type:'dy'}}"><span>订阅动态</span></router-link></div>
			<div class="listItem"><router-link :to="{name:'messageList',query:{type:'kc'}}"><span>课程更新</span></router-link></div>
			<div class="listItem"><router-link :to="{name:'messageList',query:{type:'xt'}}"><span>系统消息</span></router-link></div>

			<div class="radius"></div>
		</div>
		<div class="content">
			<!-- <messageList :data='messageList'></messageList> -->
			<router-view></router-view>	
		</div>
	</div>
</template>
<script>
import messageList from 'components/messageList/messageList'
	export default{
		data(){
			return {
				messageList:[],
				ac1:true,
				ac2:false,
				ac3:false
			}
		},
		created(){
			this.$router.push({path:'/message/messageList',query:{type:'dy'}})
		},
		components:{
			messageList
		},
		mounted(){
			this.$refs.title.style.width=window.innerWidth+'px'
			this.$refs.list.style.width=window.innerWidth+'px'
		},
		methods:{
			getaData(){
				this.$http.get('http://learningApp/api').then((res)=>{
						this.messageList=res.body.message
				})
			},

			
		}
	}
</script>
<style lang="stylus" scoped>
	.message
		background #f5f5f5
		h3
			height 0.45rem
			line-height 0.45rem
			font-size 0.18rem
			font-weight bold
			position fixed
			top 0
			left 0
			width 100%
			background #fff
			z-index 100
		.list
			z-index 100
			position fixed
			width 100%
			top 0.45rem
			height 0.6rem
			line-height 0.45rem
			display flex
			border-radius 10px
			background -webkit-gradient(linear, right bottom, left top, from(#35C3FC), to(#57e397))
			.listItem
				flex 1
				font-size 0.16rem
				.router-link-exact-active
					span
						opacity 1
						border-bottom 1px solid #fff
				span
					opacity 0.8
					padding-bottom 0.08rem
					color #fff
	.radius
		position absolute
		width 100%
		height 0.15rem
		border-radius 10px 10px 0 0
		background #f5f5f5	
		top 0.5rem				
			

		

</style>


