<template>
	<scroll class="messageList" ref='wrapper' :data='messageList'>
		<ul class="listbox" ref='list'>
			<li v-for='(item,index) in messageList' class='listItem' @touchstart.prevent='listStart'
			@touchmove.prevent='listMove'
			@touchend='listEnd' ref='listItem' :index='index'>
				<div class="left" ref='left'>
					<p class="p1">{{item.title}}</p>
					<p class="p2">{{item.time}}</p>
				</div>
				<div class='opreate'>
					<span class="cancle" @click='nodelete'>取消</span><span class="icon-delete" @click='del'></span>
				</div>
			</li>
		</ul>
	</scroll>
</template>
<script>
import {MessageBox,Toast} from 'mint-ui'
import scroll from 'components/base/scroll'
	export default{
		data(){
			return {
				messageList:[]
			}
		},
		created(){
			this.getaData()
			this.touch={}
		},
		components:{
			MessageBox,
			Toast
		},
		methods:{
			h(index){
				console.log(index)
			},
			del(e){
				var index = e.currentTarget.parentNode.parentNode.getAttribute("index")
				MessageBox.confirm('确定执行此操作?').then(()=>{
					var node=this.$refs.listItem
					this.messageList.splice(index,1)
					for (var i = 0; i < node.length; i++) {
						node[i].style.transform='translate3d(0,0,0)'
					}
					
				})
				Toast({
					  message: '操作成功',
					  iconClass: 'icon icon-success'
				});
				
			},
			nodelete(e){
				e.currentTarget.parentNode.parentNode.style.transform='translate3d('+0+'px,0,0)'
			},
			listStart(e){
				this.touch.init=true
				let touch=e.touches[0]
				this.touch.startX=touch.pageX
			},
			listMove(e){
				if(!this.touch.init){
					return 
				}
				let touch=e.touches[0]
				let newX=touch.pageX
				let deltaX=newX-this.touch.startX	
				this.touch.deltaX=deltaX

				if(deltaX>0){
					e.currentTarget.style.transform='translate3d('+0+'px,0,0)'
				}
				if(deltaX<-120){
					e.currentTarget.style.transform='translate3d('+(-130)+'px,0,0)'
				}
				e.currentTarget.style.transform='translate3d('+0+'px,0,0)'
			},
			listEnd(e){
				/*this.touch.init=false*/
				if(this.touch.deltaX<-30){
					e.currentTarget.style.transform='translate3d('+-130+'px,0,0)'
				}

				if(this.touch.deltaX>-30){
					e.currentTarget.style.transform='translate3d('+0+'px,0,0)'				
				}
				
			},
			getaData(){
				this.$http.get('http://learningApp/api').then((res)=>{
						this.messageList=res.body.message
				})
			},
		},
		mounted(){
			this.$refs.list.style.width=window.innerWidth+130+'px'

			
		},
		components:{
			scroll
		},
		watch:{
			'$route'(){
				let type=this.$route.query.type
				if(type==='dy'){
					this.getaData()
				}
				if(type==='kc'){
					this.$http.get('http://learningApp/api').then((res)=>{
						this.messageList=res.body.message.slice(0,10)
					})
				}
				if(type==='xt'){
					this.$http.get('http://learningApp/api').then((res)=>{
						this.messageList=res.body.message.slice(10,20)
					})
				}
			}
		}
		/*props:{
			data:{
				type:Array,
				default:[]
			}
		}*/
	}
</script>
<style lang="stylus" scoped>
	.messageList
		position fixed
		width 100%
		bottom 0
		top 1rem
		background #f5f5f5
		overflow hidden
		.list
			width 100%
			overflow hidden
		.listItem
			margin 0.1rem 1%
			background #fff
			padding 0.1rem
			font-size 0.16rem
			text-align left
			border-radius 8px
			transition all ease 0.3s
			transform translate3d(0,0,0)
			position relative
			
			.p1
				line-height 0.24rem
				font-weight bold
			.p2
				line-height 0.3rem
				height 0.3rem
			.opreate
				position absolute 
				right -0.1rem
				top 0
				width 1.3rem
				height 100%
				span
					display inline-block
					text-align center
					height 0.6rem
					line-height 0.6rem	
				span.cancle
					width 0.6rem
					text-align center
					font-size 0.16rem
					background #f5f5f5
				span.icon-delete
					width 0.6rem
					font-size 0.2rem
					color red
					background #dedede

</style>


