<template>
	<div class="column">
		<myhead :title='title' @dropDown='moreClass'>
			<div @click='column'>
				<i class="columnChange icon-category3" v-show='columShow'></i>
				<i class="columnChange icon-smalllist" v-show='!columShow'></i>
			</div>
		</myhead>
		<scroll ref='wrapper' :data='columnList'>
			<column :data='columnList' class='columnbox' :columnNum='columShow' @selectItem='getDetail'></column>
		</scroll>
		<more v-show='moreShow' :data='columnClass' :headtitle='classTitle'  @closeMore='hideMore'></more>
			
	</div>
</template>
<script>
import column from 'components/columnlist/columnlist'
import myhead from 'components/header/header'
import more from 'components/moreClass/moreClass'
import scroll from 'components/base/scroll'

	export default{
		data(){
			return{
				cNum:1,
				columShow:false,
				moreShow:false,
				title:'推荐文章',
				classTitle:'文章分类',
				columnList:[],
				columnClass:[{
					label:'全部',
					value:'全部'
				},
				{
					label:'人文地理',
					value:'人文地理'
				},
				{
					label:'黑科技',
					value:'黑科技'
				},
				{
					label:'民俗风情',
					value:'民俗风情'
				},
				{
					label:'吃喝游玩',
					value:'吃喝游玩'
				},
				{
					label:'风土人情',
					value:'风土人情'
				},
				{
					label:'人与自然',
					value:'人与自然'
				}]
			}
		},
		created(){
			console.log('sss')
			this.getData()
		},
		methods:{
			getData(){
				this.$http.get('http://learningApp/api').then((res)=>{
						this.columnList=res.body.recomends
				})
				/*this.$http.get('api/column').then(function(response){
					if(response.body.error===0){
						this.columnList=response.body.data
					}
				})*/
			},
			moreClass(){
				this.moreShow=!this.moreShow
			},
			hideMore(value){
				console.log(value)
				this.title=value
				this.moreShow=false
			},
			column(){
				this.columShow=!this.columShow

			},
			getDetail(item){
				console.log(item)
				this.$router.push({path:'/column/columnDetail',query:{url:item.url,title:item.title,des:item.des,tags:item.tags,avata:item.avata}})
			}
		},
		components:{
			column,
			myhead,
			more,
			scroll
		}
	}
</script>
<style lang="stylus" scoped>
	.columnbox
	
		background #fff
		padding 0.05rem 0
		border-radius 10px
	.columnChange
		font-size 0.25rem
		position absolute
		left 0.1rem
		top 0.12rem
	.wrapper
		top 0.45rem 
		position fixed
		width 100%
		left 0
		right 0
		bottom 0
	
</style>



