import Mock from 'mockjs'
export default Mock.mock('http://learningApp/api',{
	'course|10-50':[
			{
				"url":"@image('400x300','#35c3fc', '#FFF', 'Mock.js')",
				"avata":"@image('100x100','#57e397')",
				"price|50-300":187,
				"title":"@ctitle(8,10)",
				"des":"@ctitle(8,10)"
			}
	],
	'recomends|10-50':[
		{
			"url":"@image('400x300','#35c3fc', '#FFF', 'Mock.js')",
			"title":"@ctitle(8,10)",
			"tags|1-2":["地理","摄影"],
			"avata":"@image('100x100','#57e397')",
			"des":"@ctitle(8,20)",
			"time":"@date('yyyy-MM-dd')"
		}
	],
	"message|10-30":[
		{
			"title":"@ctitle(8,10)",
			"time":"@date('yyyy-MM-dd')"
		}
	],
	"comment|10-30":[
		{
			"avata":"@image('100x100','#57e397')",
			"user":"@name",
			"text":"@ctitle(20,40)",
			"time":"@date('yyyy-MM-dd')"	
		}
	]
})



